import { Page } from "components/page";
import { Typography } from "@alfalab/core-components/typography";
import "./styles.css";
import { Link } from "react-router-dom";
import madeInAlfaImg from "imgs/Made_in_Alfa.jpeg";
import ownDesignImg from "imgs/Own_design.jpeg";

export const Homepage = () => {
	const linkStyle = { textDecoration: "none", color: "inherit" };

	return (
		<Page>
			<div className="homepage">
				<Link to="/made-in-alfa" style={linkStyle}>
					<div className="homepage-block">
						<img className="homepage-img" src={madeInAlfaImg} alt="  " />
						<Typography.Title tag="h1" className="homepage-block-text" font="system">
							  
						</Typography.Title>
					</div>
				</Link>
				<Link to="/own-design" style={linkStyle}>
					<div className="homepage-block">
						<img className="homepage-img" src={ownDesignImg} alt=" " />
						<Typography.Title tag="h1" className="homepage-block-text" font="system">
							 
						</Typography.Title>
					</div>
				</Link>
			</div>
		</Page>
	);
};
